@import url(https://unpkg.com/bootstrap@4.1.0/dist/css/bootstrap.min.css);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 767px) {
    .detailBlock {
        width: 100%;
    }
    .help_cov {
        margin-bottom: 15px;
    }
}

@media (min-width: 768px) {}

@media only screen and (min-device-width: 768px) and (max-device-width: 1023px) {
    .detailBlock {
        width: 60%;
    }
}
.secGap {
    padding: 50px 0;
}

.secTitle {
    margin-bottom: 20px;
}

.vMore {
    padding: 14px 35px !important;
    background: #ffc600 !important;
    margin-right: 10px !important;
    color: #000 !important;
    font-weight: 600 !important;
    border-radius: 25px !important;
    text-decoration: underline !important;
    display: inline-block;
}

.vMore:hover {
    background: #f9cd33 !important;
}

#topRibbon {
    padding: 10px 0;
}

#topRibbon a {
    font-size: 15px;
    color: #00857a;
    font-weight: 700;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

#mainFace {
    background: #f0f2f4;
    padding: 25px 0;
}

#mainFace .secGap {
    position: relative;
}

#mainFace a {
    color: #000;
}

.modal-body {
    background: #f1f2f4;
}

.modal-title {
    width: 100%;
    text-align: center;
    font-size: 22px;
    font-weight: 600;
}

.modal-header {
    padding: 10px 15px;
}

.modal-content {
    border-radius: 0;
    box-shadow: 0 0 6px rgba(0, 0, 0, .25), 0 10px 20px rgba(0, 0, 0, .2);
    border: 0;
}

.modal.show .modal-dialog {
    min-height: calc(100vh - 100px);
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.modal-backdrop.show {
    opacity: .9;
}

.modal-backdrop {
    background-color: #fff;
}

.close {
    color: #000;
    text-shadow: 0 1px 0 #fff;
    opacity: 1;
    font-size: 2.2rem;
}

#mainFace h1 {
    line-height: 1.3;
    font-size: 2.2rem;
    font-weight: 800 !important;
}

#mainFace p {
    font-size: 18px;
    font-weight: 600;
    padding: 15px 0;
}

#mainFace .logo a {
    text-decoration: underline;
    font-weight: 700;
}

#mainFace .logo a:hover {
    text-decoration: none;
}

#mainFace .vMore.watch {
    background: #fff !important;
}

#mainFace .vMore.watch i {
    margin-right: 7px;
}

.mobile {
    max-width: 59%;
    position: absolute;
    right: -130px;
    bottom: -135px;
}

.logo {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo img {
    height: 95px;
}

#offer, #footNav {
    background: #00cfbb;
}

#offer {
    padding: 90px 0;
    text-align: center;
}

#offer h2 {
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 1.25;
}

#offer h3 {
    font-size: 1.125rem;
    font-weight: 600 !important;
    line-height: normal;
}

#offer i {
    font-size: 50px;
    margin-bottom: 20px;
}

#offer p {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

#testimonial {
    background: #f7f7f9;
    text-align: center;
    padding: 60px 0;
    position: relative;
}

.testiModal {
    position: absolute;
    bottom: -60;
    left: 0;
}

#testimonial h4 {
    font-size: 17px;
    font-weight: 600 !important;
    margin-bottom: 15px;
}

blockquote {
    font-size: 1.5rem;
}

#needTalk h3 {
    font-size: 24px;
    margin-bottom: 15px;
    line-height: 1.4;
    font-weight: 700 !important;
}

#needTalk .row {
    flex-direction: row;
    align-items: center;
}

.needTalk {
    text-align: right;
}

.needTalk img {
    height: 220px;
}

#footBTM {
    padding: 20px 0;
    background: #000;
    color: #fff;
}

#footBTM p {
    margin-bottom: 0;
}

#footNav {
    padding: 25px 0;
}

#footNav ul {
    padding: 0;
}

#footNav ul li:not(:last-child) {
    margin-right: 20px;
}

#footNav ul li {
    list-style: none;
    float: left;
}

#footNav ul li a {
    text-decoration: underline;
    font-size: 15px;
    font-weight: 700;
}

#footNav ul li a:hover {
    text-decoration: none;
}

@media (min-width: 1200px) {
    .container {
        max-width: 925px !important;
    }
}

@media (min-width: 576px) {
    .modal-dialog {
