html {
    height: 100%;
    position: relative;
}

body {
    font-family: 'Montserrat', sans-serif !important;
    font-size: 1rem;
    font-style: normal;
    font-weight: 400;
    line-height: 1.375;
}

.no-mar-pad {
    margin: 0 !important;
    padding: 0 !important;
}

.no-mar-padright {
    padding-right: 0;
}

.no-mar-padleft {
    padding-left: 0;
}

.col-centered {
    float: none !important;
    margin: 0 auto !important;
}

a, a:hover, a:focus {
    color: inherit;
    text-decoration: none;
    transition: all 0.3s;
}

p {
    /* font-family: 'HelveticaNeueLTPro-Bd'; */
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.4;
    letter-spacing: normal;
}

h1, h2 {
    font-weight: 700 !important;
}

h3, h4 {
    font-weight: 600 !important;
}

